.messageContainer{
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    width: 80%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 50px;
    margin-top: 15px;
    margin-bottom: 15px;
    border-bottom: 1px solid gainsboro;
    cursor: pointer;   
}


.from{
    text-align: right;
    word-wrap: break-word;
}

.message{
    word-wrap: break-word;
}